* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: Helvetica, sans-serif;
	background: #3498db;
}

.app {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background: #ecf0f1;
	border-radius: 10px;
	padding: 20px;
	min-width: 400px;
	max-width: 600px;
	box-shadow: 0 0 8px rgba(0,0,0,.4);
}

.commentsList {
	list-style-type: none;
}

.commentItem {
	padding: 10px;
    background: #bdc3c7;
    color: #34495e;
    border-radius: 10px;
	margin: 10px 0;
	word-wrap: break-word;
}

.commentItem-buttons {
	display: flex;
	margin-top: 5px;
}

.commentItem-buttons span {
	margin-left: auto;
}

.btn {
	border: none;
	outline: none;
	background: none;
	font-size: 14px;
	transition: .4s;
	cursor: pointer;
}

.btn-up {
	color: #2ecc71;
}

.btn-up:hover {
	color: #27ae60;
}

.btn-down {
	color: #e74c3c;
	margin: 0 10px;
}

.btn-down:hover {
	color: #c0392b;
}

.btn-remove {
	opacity: .6;
}

.btn-remove:hover {
	opacity: 1;
}

.commentAdd {
	display: flex;
	flex-direction: column;
	margin-top: 40px;
}

.commentAdd-btn {
	outline: none;
	border: none;
	border: 2px solid #1abc9c;
	border-radius: 10px;
	color: #1abc9c;
	padding: 10px 12px;
	text-transform: uppercase;
    background: none;
    cursor: pointer;
    transition: .4s;
    font-weight: bold;
    margin-top: 10px;
    align-self: center;
}

.commentAdd-btn:hover {
	color: #fff;
	background: #1abc9c;
}

.commentAdd-input {
	display: block;
    resize: none;
    min-height: 120px;
    padding: 10px;
    outline: none;
    border: none;
}